#{$all-text-inputs},
textarea,
select {
  appearance: none;
  border: 1px solid $color-grey-mid;
  border-radius: 0;
  display: block;
  max-width: 100%;
  min-width: 200px;
  width: 100%;

  @include media($bp-tablet-up) {
    width: auto;
  }

  .error & {
    border-color: $color-red;
  }
}

select {
  @include appearance(none);
  background-color: $color-white;
  background-image: url('/wp-content/themes/access/assets/img/arrow-down.svg');
  background-position: right .5rem center;
  background-repeat: no-repeat;
  background-size: .8rem;
  padding: (.75 * $spacing-base) 1.8rem (.75 * $spacing-base) (.75 * $spacing-base);
}

fieldset {
  border-width: 0;
  padding: 0;
}

[type='checkbox'],
[type='radio'] {
  display: inline;
}

#{$all-text-inputs},
textarea {
  padding: (.75 * $spacing-base);
}

input[type="number"] {
  @include appearance(textfield);

  &::-webkit-outer-spin-button,
  &::-webkit-inner-spin-button {
    @include appearance(none);
    margin: 0;
  }
}
