$search-height: 70px;
$control-width: 33%;

.map {
  height: 100vh;
  position: relative;

  @include media($bp-below-tablet) {
    height: auto;
  }
}

.map-controls {
  @include display(flex);
  @include flex-direction(column);
  @if $text-direction == 'rtl' {
    @include position(absolute, 0 0 0 null);
  } @else {
    @include position(absolute, 0 null 0 0);
  }

  width: $control-width;

  @include media($bp-below-tablet) {
    @include position(relative, 0 0 0 0);
    display: block;
    width: 100%;
    z-index: 1;
  }
}

.map-mapbox {
  @if $text-direction == 'rtl' {
    @include position(absolute, 0 $control-width 0 0);
  } @else {
    @include position(absolute, 0 0 0 $control-width);
  }

  @include media($bp-below-tablet) {
    @include position(static, 0 0 0 0);
    height: 50vh;
    max-height: 450px;
    width: 100%;
  }
}

.map-results-container {
  @include flex(1 1 auto);
  overflow: scroll;
  position: relative;

  @include media($bp-below-tablet) {
    overflow: auto;
  }
}

.map-location-list {
  list-style: none;
  margin: 0;
  padding: 0;

  li {
    @include transition(background .1s ease-in);
    @if $text-direction == 'rtl' {
      padding: $spacing-base (3 * $spacing-base) $spacing-base $spacing-base;
    } @else {
      padding: $spacing-base $spacing-base $spacing-base (3 * $spacing-base);
    }
    background: $color-white url('/wp-content/themes/access/assets/img/map-pin-blue.png') $spacing-base (1.3 * $spacing-base) no-repeat;
    background-size: 33px 40px;
    border-bottom: 1px solid $color-grey-light;
    cursor: pointer;

    &.active {
      background-color: $color-yellow-light;
    }
  }

  .link-more {
    font-size: $font-size-small;
    font-weight: $font-weight-bold;
  }

  .loading & {
    display: none;
  }
}

.map-loading,
.map-no-results {
  @include typography-supplemental();
  display: none;
  padding: $spacing-base;
  text-align: center;
}

.loading .map-loading,
.no-results .map-no-results {
  display: block;
}

.map-search {
  height: $search-height;
  padding: $spacing-base;
  width: 100%;
}

.map-location-name {
  margin: 0;
}

.map-location-details {
  @include typography-supplemental();
  font-size: $font-size-small();
}

.map-filter-header {
  border-radius: 0;
  font-size: small;
  padding: $spacing-base;

  &::after {
    @include position(relative, -.1em null null null);
    @include size(0 0);
    @include triangle(.8em .6em, $color-white, down);
    content: '';
    display: inline-block;
    margin-left: .5em;
  }

  &.active {
    &::after {
      @include triangle(.8em .6em, $color-white, up);
      border-top: 0;
    }
  }
}

.map-filter-container {
  border-bottom: 1px solid $color-grey-light;
}

.map-filter-list,
.map-filter-program-list {
  list-style: none;
  margin: 0;
}

.map-filter-list {
  padding: 0 $spacing-base;
}

.map-filter-program-list {
  padding: 0;
}

.map-filter-category {
  border-bottom: 1px solid $color-grey-light;
  font-weight: $font-weight-bold;
  padding: $spacing-base 0;

  &:last-child {
    border: 0;
  }
}

.map-filter-category,
.map-filter-program {
  label {
    display: inline-block;
    padding-left: $spacing-base;
    position: relative;

    input {
      @include position(absolute, .4em null null 0)
    }
  }
}

.map-filter-program {
  font-size: $font-size-small;
  font-weight: $font-weight-normal;
  padding-top: .5 * $spacing-base;
}

.map-filter-toggle {
  @include size(0 0);
  @include triangle(.6em .4em, $color-blue, down);
  display: inline-block;
  float: $text-direction-end;
  margin-top: .5em;

  &.active {
    @include triangle(.6em .4em, $color-blue, up);
    border-top: 0;

    .map-filter-collapse {
      display: inline;
    }

    .map-filter-expand {
      display: none;
    }
  }

  .map-filter-collapse {
    display: none;
  }
}

.map-filter-programs,
.map-filter-body {
  @include transition(max-height .2s ease-in);
  max-height: 0;
  overflow: hidden;

  &.active {
    max-height: 1000px;
  }
}

// This margin amount is a hack fix to adjust the positioning of the Google
// auto-complete box. The value is found from trial-and-error.
.pac-container {
  margin-top: -30px;
}
