.program-detail-title {
  @include media($bp-mobile-up) {
    @include align-items(flex-start);
    @include display(flex);
  }
  margin-bottom: $spacing-base / 2;
}

.program-detail-subtitle {
  @include typography-base;
}

.program-landing-body,
.program-detail-body {
  aside {
    @include media($bp-desktop-up) {
      @include span-columns(3);
    }
  }

  .content-body {
    @include media($bp-desktop-up) {
      @include span-columns(9);
      @include omega();
    }
  }
}

.program-cards {
  @include full-width-home-margins;
  background: $color-grey-lightest;
  margin-bottom: $spacing-base;
  // .1px is to fix a padding bug where the grey background doesn't show.
  padding: $spacing-base $spacing-base .1px;
}

.program-card {
  background: $color-white;
  margin-bottom: $spacing-base;
  padding: $spacing-base;

  @include media($bp-mobile-up) {
    @include align-items(flex-start);
    @include display(flex);
    padding: 1.5*$spacing-base;
  }
}

.program-card-title {
  margin-bottom: 0;

  .program-label {
    display: none;

    @include media($bp-mobile-up) {
      display: inline-block;
    }
  }
}

.program-card-summary {
  :last-child {
    margin-bottom: 0;
  }
}

.program-pagination {
  text-align: $text-direction-end;

  .btn {
    margin-#{$text-direction-start}: .5*$spacing-base;
  }
}

.program-no-results {
  @include typography-blockquote();

  :last-child {
    margin-bottom: 0;
  }
}

.program-icon {
  $icon-height: 50px;
  $icon-width: 50px;
  margin-bottom: $spacing-base;

  .icon {
    @include size($icon-width $icon-height);
    background: $color-blue-bright; // Fake style for demo purposes
    border-radius: $icon-height/2; // Fake style for demo purposes
    display: inline-block;
    font-size: 0; // Fake style for demo purposes
    vertical-align: middle;
  }

  @include media($bp-mobile-up) {
    padding-#{$text-direction-end}: $spacing-base;

    .program-label {
      display: none;
    }
  }
}

.program-header-icon {
  margin-bottom: 0;
}

.program-detail-lead {
  @include typography-lead;
  margin-bottom: $spacing-base * 2;
}

.program-detail-heads-up {
  > ul {
    list-style: none;
    padding: 0;

    > li {
      padding-#{$text-direction-start}: $spacing-base * 3;
      padding-bottom: $spacing-base * .75;
      padding-top: $spacing-base * .75;
    }
  }
}

.program-descriptor {
  @include typography-h1()
  margin-bottom: 2px;
}

.program-information {
  @include typography-supplemental();
}

.program-nav {
  @include typography-nav();
  margin-bottom: 40px;

  @include media($bp-desktop-up) {
    ol {
      list-style-position: inside;
      padding-#{$text-direction-start}: 0;
    }
  }

  li {
    margin-bottom: 14px;
    @include media($bp-desktop-up) {
      border-bottom: 1px solid $color-blue-dark;
      margin-bottom: $spacing-base;
      padding-bottom: $spacing-base;

      &:last-of-type {
        border-bottom: 0;
        margin-bottom: 0;
        padding-bottom: 0;
      }
    }
  }

  .active {
    font-weight: bold;
    text-decoration: none;
  }
}

.program-detail-content {
  margin-bottom: 30px;

  .btn {
    font-size: $font-size-small;
  }
}

.program-next-section {
  h4 {
    font-size: $font-size-small;
    margin-bottom: 5px;
  }
}

.program-related-categories {
  h5 {
    font-size: $font-size-small;
    margin-bottom: 0;
  }
}

.program-share-links {
  border-top: 4px solid $color-blue-dark;
  padding-top: $spacing-base;
  text-align: $text-direction-end;

  .icon {
    display: inline-block;
    height: $spacing-base;
    margin-#{$text-direction-end}: $spacing-base / 2;
    vertical-align: middle;
    width: $spacing-base;
  }

  @include media($bp-desktop-up) {
    padding-top: $spacing-base * 2;
    text-align: $text-direction-start;
  }
}

.program-print-page {
  margin-#{$text-direction-end}: $spacing-base;
  @include media($bp-desktop-up) {
    display: block;
    margin-#{$text-direction-end}: 0;
    margin-bottom: $spacing-base;
  }
}

.quick-feedback {
  button {
    @include typography-h4();
    background-color: $color-white;
    border: 1px solid $color-blue-dark;
    border-radius: 10px;
    font-size: $font-size-small;
    line-height: 1;
    margin: ($spacing-base * .5) ($spacing-base * .5) 0 0;
    padding: $spacing-base * .5;
  }
}

.program-detail-body-print {
  .content-body {
    float: none;
    width: 100%;
  }

  .program-detail-help-container {
    display: block;
    flex-flow: column nowrap;
  }
}

.program-detail-step {
  height: 0;
  opacity: 0;
  overflow: hidden;

  &.active,
  &.program-detail-print-step {
    height: auto;
    opacity: 1;
    overflow: visible;
  }
}

.program-detail-help {
  border-top: 2px solid $color-yellow-access;
  clear: $text-direction-start;
  margin: ($spacing-base * 1.5) 0;
  padding: ($spacing-base * 1.5) 0;

  .program-detail-body-print & {
    border-top: 0;
    padding: 10px 0;
  }
}

.program-detail-help-container {
  display: flex;
  flex-flow: row wrap;
}

.program-detail-help-method {
  @include child-margin-edges;

  p {
    &:first-of-type {
      margin-bottom: $spacing-base / 3;

      strong {
        @include typography-h3;
        font-size: $font-size-base;
      }
    }
  }

  h3 {
    @include typography-h3;
    font-size: $font-size-base;
    margin-bottom: $spacing-base / 3;
  }

  @include media($bp-desktop-up) {
    @include flex-grid(2, $site-margins * 2);
    margin-bottom: $spacing-base * 1.5;
  }
}

.last-updated {
  @include typography-supplemental();
  margin: 2 * $spacing-base 0;
}

.program-detail-eligibility {
  > ul {
    list-style: none;

    > li {
      background-image: url('src/img/icon-eligibilitycheck.svg');
      background-position: 0 3px;
      background-repeat: no-repeat;
      background-size: auto 26px;
      margin: 30px 0;
      padding-#{$text-direction-start}: $spacing-base * 2;
    }
  }

  li {
    li {
      list-style: disc;
      margin: ($spacing-base * .75) 0;
    }
  }
}

.program-detail-status {
  display: flex;
  margin-bottom: $spacing-base;
  padding: $spacing-base;
}

.program-detail-status-icon {
  margin-#{$text-direction-end}: $spacing-base;
  position: relative;
  width: $program-detail-icon-size;

  &:after {
    @include size($program-detail-icon-size);
    background: $color-black;
    border-radius: 50%;
    color: $color-white;
    content: 'i';
    display: block;
    font-family: $font-family-serif;
    font-size: $font-size-medium;
    font-weight: $font-weight-bold;
    text-align: center;
  }
}

.program-detail-status-buttons {
  .btn {
    margin-#{$text-direction-end}: $spacing-base;

    &:last-child {
      margin-#{$text-direction-end}: 0;
    }
  }
}

.program-detail-what-you-need-to-include {
  @include transition(all 1s);
  background-color: $color-grey-lightest;
  margin: $spacing-base 0;
  max-height: $spacing-base * 3;
  overflow: hidden;

  &.open,
  &.no-js-open {
    max-height: 12000px;
    overflow: visible;
  }
}

.program-detail-what-you-need-to-include-header {
  align-items: center;
  background-color: $color-blue-dark;
  color: $color-white;
  display: flex;
  height: $spacing-base * 3;
  padding: $spacing-base;

  p {
    flex: 1;
    font-weight: $font-weight-bold;
    margin-bottom: 0;
  }
}

.hide-or-show-list {
  cursor: pointer;
  font-style: italic;

  &.hide {
    .show-list {
      display: none;
    }

    .hide-list {
      display: block;
    }
  }

  &.show {
    .show-list {
      display: block;
    }

    .hide-list {
      display: none;
    }
  }
}

.no-js-hidden {
  display: none;
}

.program-detail-what-you-need-to-include-body {
  padding: ($spacing-base * 2) $spacing-base ($spacing-base * 1.5);

  h5 {
    color: $color-black;
    font-weight: bold;
    margin-top: $spacing-base * 2;
  }

  li {
    margin: .25rem 0;
  }
}

.program-detail-status-text {
  .icon-screening {
    @include size($spacing-base);    height: 21px;
    margin-#{$text-direction-end}: $spacing-base/2;
    vertical-align: sub;
  }
}

.program-detail-application-method {
  margin: $spacing-base*2 0;
}

.program-detail-print-step {
  margin: $spacing-base*2 0;

  &:first-of-type {
    margin-top: 0;
  }

  &:last-of-type {
    margin-bottom: 0;
  }
}

.back-to-program-start {
  margin: $spacing-base*2 0 0;
}
